.header {
	height: 70px;
	background: #161616;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	z-index: 999;

	.inner {
		height: 70px;
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	.menu {
		margin: 0;
		padding-left: 60px;
		width: 100%;
		height: 70px;
		max-width: 700px;
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	.link {
		color: #fff;
		cursor: pointer;
		&:hover {
			color: rgb(184, 184, 184);
		}
	}

	@media (max-width: 500px) {
		.menu {
			width: 100%;
			padding: 0;
		}
	}
	@media (min-width: 500px) {
		.menu :nth-child(1) {
			display: none;
		}
	}
}
@media (max-width: 500px) {
	.link {
		font-size: 13px;
	}
}
